*{ margin:0;
  padding:0;
  box-sizing: border-box;
}
body{
 
 
  background: linear-gradient(rgb(29, 165, 255),rgb(34,67,13));
  height:100vh;
  font-family: Arial, Helvetica, sans-serif;
  width:100%;
  color:#fff;
  /* background-size: cover; */
}

.container{
  width:500px;
  margin:0 auto;
  padding:10px;
  text-align: center;
  
}
.container h1{
  
  padding:15px 0px;
  
}
.input_text
{
  height:35px;
  width:300px;
  border: 0;
  outline: none;
  margin:0 50px;
  padding-left: 10px;
  font-size: 16px;
  font-weight: bold;
  text-transform: capitalize;
  box-sizing: border-box;


}
::placeholder{
  color: #d4d4d4;
}
.btn{
  padding: 10px 30px;
  margin: 10px 0px;
  border: none;
  outline: none;
  background: transparent;
  border: 2px solid #fff;
  color:#fff;
  cursor: pointer;
  transition: 0.3s  ease-in-out;

}
.btn:hover{
  background: #fff;
  color:#000;
}
.showdata{
  margin: 80px 0px;
  line-height: 1.6;
}
.showdata ul{
  list-style-type:none;
}
.showdata .city{
  font-size: 5rem;
}
.showdata .temp{
  font-size:4rem ;
  font-weight: bold;
}
.showdata .desc{
  font-size: 2rem;
  text-transform: capitalize;
}
.spa{
  border-radius: 100%;
  
  color:black;
  background-color: white;
  
}
